.btn-flex-center {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-item-action {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background: linear-gradient(to right, rgb(205, 133, 0), var(--color-secondary));
  color: var(--color-white);
  &:hover {
    color: var(--color-white);
  }
}